export function NewContact($timeout) {
    return {
        restrict: 'E',
        template: require('./templates/new-contact.html'),
        replace: true,
        scope: {
            model: "=model",
            add: "&add",
            config: "=config"
        },
        link: function () {
            $timeout(() => {
                $('.tooltipped').tooltip({ delay: 50 });
            }, 0);
        }
    };
}
NewContact.$inject = ['$timeout'];



