export class Dictionary {
    constructor() {
        this.count = 0;
        this.dict = {};
    }
    set(key, value) {
        if (this.dict[key] == undefined)
            this.count++;
        this.dict[key] = value;
        return value;
    }
    get(key, default_value) {
        return this.dict[key] || default_value;
    }
    has(key) {
        return this.dict[key] != undefined;
    }
    getOrCreate(key, default_value) {
        var result = this.dict[key];
        if (result == undefined) {
            result = default_value;
            this.dict[key] = result;
            this.count++;
        }
        return result;
    }
    get keys() {
        return Object.keys(this.dict);
    }
    remove(key) {
        var item = this.dict[key];
        if (item != undefined) {
            delete this.dict[key];
            this.count--;
        }
        return item;
    }
}
export class NumDictionary extends Dictionary {
    increment(key, inc = 1) {
        var count = this.getOrCreate(key, 0);
        this.set(key, count + inc);
    }
}
export class Number {
    static ZeroLeadString(num) {
        var s = num.toString();
        if (s.length < 2)
            return "0" + s;
        return s;
    }
}
export class ExtArray extends Array {
    last() {
        if (this.length == 0)
            return undefined;
        return this[this.length - 1];
    }
    remove(item) {
        var index = this.indexOf(item);
        if (index > -1) {
            this.splice(index, 1);
            return true;
        }
        return false;
    }
}
function GetStringId(o) {
    if (o.id)
        return o.id();
    return o.toString();
}
export class UniqList extends ExtArray {
    constructor(items) {
        super();
        this.dict = new Dictionary();
        items.forEach((item) => {
            this.push(item);
        });
    }
    push(item) {
        if (!this.dict.has(GetStringId(item))) {
            this.dict.set(GetStringId(item), item);
            return super.push(item);
        }
    }
    pop() {
        var item = super.pop();
        if (item)
            this.dict.remove(GetStringId(item));
        return item;
    }
    remove(item) {
        if (this.dict.remove(GetStringId(item)) != undefined) {
            var index = this.indexOf(item);
            if (index > -1) {
                this.splice(index, 1);
                return true;
            }
        }
        return false;
    }
    get(id) {
        return this.dict.get(id);
    }
    contains(item) {
        return this.contains_id(GetStringId(item));
    }
    contains_id(id) {
        return this.dict.has(id);
    }
    contains_by(filter) {
        for (var i = 0; i < this.length; i++) {
            if (filter(this[i])) {
                return true;
            }
        }
        return false;
    }
    include(list) {
        for (var i = 0; i < list.length; i++) {
            if (!this.contains(list[i]))
                return false;
        }
        return true;
    }
    to_string() {
        return this.map((item) => { return GetStringId(item); });
    }
}



