export function TagsFilterList() {
    return {
        restrict: 'E',
        scope: {
            model: "=model",
            click: "&click",
            position: "@position",
            create: "&create",
        },
        template: require("./templates/tags-filter-list.html"),
        replace: true,
        link: function (scope, element, attrs) {
            scope.hasCreate = attrs['create'] != undefined;
        }
    };
}



