export function Schedule() {
    return {
        restrict: 'E',
        transclude: true,
        template: require('./templates/schedule.html'),
        replace: true,
        scope: {
            sched: "=model",
        },
        compile: function () {
            return {
                pre: function (scope) {
                    scope.time_re = /^([01]\d|2[0-3]):?([0-5]\d)$/;
                    scope.$watch('sched.everyday', (newValue) => {
                        if (newValue)
                            angular.forEach(scope.sched.json.days, (d) => {
                                d.enabled = true;
                            });
                    });
                    scope.$watch('sched.all_day', (newValue) => {
                        if (newValue) {
                            scope.sched.startTime = '00:00';
                            scope.sched.endTime = '23:59';
                        }
                    });
                }
            };
        }
    };
}



