import { UniqList } from './core';
export class Tag {
    constructor(value) {
        this.value = value;
        this.color = Tag.GetColor(value);
    }
    id() {
        return this.value;
    }
    static GetColor(value) {
        if (value == 'WARN')
            return "#cccc32";
        if (value == 'ERROR')
            return "#cc0032";
        if (value == 'OK')
            return "#33cc99";
        if (value == 'EXCEPTION')
            return "#e14f4f";
        if (value == 'NODATA')
            return "lightgray";
        var hash = 0, i, chr, len;
        if (value.length == 0)
            return Tag.Colors[0];
        for (i = 0, len = value.length; i < len; i++) {
            chr = value.charCodeAt(i);
            hash = ((hash << 5) - hash) + chr;
            hash |= 0; // Convert to 32bit integer
        }
        return Tag.Colors[Math.abs(hash) % Tag.Colors.length];
    }
}
Tag.Colors = ["#FFEBEE", "#FCE4EC", "#EDE7F6", "#E8EAF6", "#E1F5FE", "#E0F7FA", "#E8F5E9", "#F1F8E9", "#FFFDE7", "#FFF8E1", "#FBE9E7", "#EFEBE9", "#ECEFF1"];
export class TagList extends UniqList {
    constructor(tags) {
        super(tags.map((t) => { return new Tag(t); }));
    }
}
export class TagFilter {
    constructor(selection) {
        this.selection = selection;
        this.value = "";
        this.open = false;
        this.filtered = new UniqList([]);
        this.items_hidden = 0;
    }
}



