export function Contact() {
    return {
        restrict: 'E',
        template: require('./templates/contact.html'),
        replace: true,
        scope: {
            contact: "=contact",
            click: "&click",
            delete: "&delete",
            clickEnabled: "@clickEnabled"
        },
        link: function (scope, element, attrs) {
            scope.hasClick = attrs['click'] != undefined;
            scope.hasDelete = attrs['delete'] != undefined;
        }
    };
}



