export function Tag() {
    return {
        restrict: 'E',
        scope: {
            remove: "&",
            item: "=item",
            disabled: "@disabled"
        },
        template: require("./templates/tag.html"),
        replace: true,
        link: function (scope, element, attrs) {
            scope.hasRemove = attrs['remove'] != undefined;
        }
    };
}



