export function ApiStatus(api) {
    return {
        restrict: 'E',
        transclude: true,
        template: require("./templates/api-status.html"),
        replace: true,
        link: function (scope) {
            scope.api_status = api.status;
        }
    };
}
ApiStatus.$inject = ['api'];



