import * as moment from 'moment';
export function Timestamp() {
    return {
        restrict: 'E',
        transclude: true,
        scope: {
            timestamp: "=timestamp",
        },
        template: require("./templates/timestamp.html"),
        replace: true,
        link: function (scope) {
            if (scope.timestamp)
                scope.ago = moment.utc(scope.timestamp.date).fromNow();
        }
    };
}



