import { TagFilter } from '../models/tag';
export function SubEditor() {
    return {
        restrict: 'E',
        transclude: true,
        template: require('./templates/subeditor.html'),
        replace: true,
        scope: {
            sub: "=sub",
            contacts: "=contacts",
            tags: "=tags",
        },
        compile: function () {
            return {
                pre: function (scope) {
                    scope.$watch('sub', (sub) => {
                        scope.tags_filter = new TagFilter(sub.tags);
                    });
                }
            };
        }
    };
}



